.go-top__squer {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background: #ff5a30;
  position: relative;
  overflow: hidden;
}
.go-top__squer::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  border-left: 3px solid #fff;
  border-top: 3px solid #fff;
}
@media (max-width: 992px) {
  .go-top__squer {
    width: 15px;
    height: 25px;
  }
  .go-top__squer::before {
    width: 10px;
    height: 10px;
  }
}
@media (max-width: 992px) {
  .go-top {
    flex-direction: column-reverse;
    right: 5px;
    align-items: center;
  }
}

.success-block {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 0;
  left: -1px;
  text-align: center;
  background-color: #1e212c;
  border: 1px solid #ff5a30;
  border-radius: 6px;
  box-shadow: 0 0 10px 1px rgba(255, 90, 48, 0.6);
  z-index: 100;
  transition: all 0.5s ease;
  transform: scaleY(0);
}
.success-block h5 {
  margin: 40px 0 0;
  font-size: 40px;
  -webkit-user-select: none;
          user-select: none;
  font-family: "Ubu-B";
  color: #ff5a30;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
  letter-spacing: 0.2rem;
}
.success-block p {
  margin: 50px 30px 0;
  font-size: 30px;
  line-height: 130%;
}
.success-block p::before {
  content: attr(data-success-text);
  color: #fff;
}
.success-block button {
  margin: 100px 0 40px;
  align-self: center;
